import React from "react";

export type SubjectsProps = {
    sum: number,
    text: string,
    hidden: boolean
}

const SubjectBox: React.FC<SubjectsProps> = (props: SubjectsProps) => {
    return (
        <div>
            <h3></h3>
            <p>{props.sum}{props.text}</p>
        </div>
    )
}

export default SubjectBox;